export function wrapAddPlayerPayload(message) {
    return {
        topic: 'player',
        subtopic: 'add',
        data: message
    };
}
export function getPlayersPayload() {
    return {
        topic: 'player',
        subtopic: 'get',
    };
}
export function createGetSettingsPayload() {
    return {
        topic: 'settings',
        subtopic: 'get',
    };
}
export function getAddWordPayload(word) {
    return {
        topic: 'game',
        subtopic: 'add',
        data: word
    };
}
